.cardTemperature {
  font-size: 8em;
  line-height: 0;
  font-weight: 300;
  margin-left: -0.025em;
  margin-bottom: 0.4em;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  &:after {
    content: '°';
    position: relative;
    font-size: .5em;
    top: -.7em;
    right: 0;
  }
}

.cardMinTemp{
  font-size: 2em;
  margin-left: -0.025em;
  margin-bottom: 0.4em;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black; 
  &:before {
    content: 'Low: '
  }
  &:after {
    content: '°';
  }
}

.cardMaxTemp{
  font-size: 2em;
  margin-left: -0.025em;
  margin-bottom: 0.4em;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;  
  &:before {
    content: 'High: '
  }
  &:after {
    content: '°';
  }
}