.cardUpperPortion {
  background-size: auto 100%;
  background-position: center;
  z-index: 1;
  transition: transform .2s ease-in;
  will-change: transform;
  overflow: hidden;
  box-shadow: 0 22px 22px -22px #000; }
  .isActive .cardUpperPortion {
    transform: translateY(-115px); }
  .cardUpperPortion .cardInfo {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 10px;
    color: #fff;
    font-weight: 300; }

.cardBottomPortion {
  margin-top: 0;
  z-index: 0;
  opacity: 0;
  transform: scale(0.95);
  transform-origin: 50% 50%;
  transition: transform .2s ease-in, opacity .2s ease-in;
  will-change: transform, opacity;
  box-shadow: 0 30px 20px -10px #111; }
  .cardBottomPortion .cardDetail {
    position: absolute;
    bottom: 0;
    height: 100px;
    width: 100%;
    color: #555;
    display: flex;
    flex-direction: column;
    justify-content: flex-end; }
  .cardBottomPortion::before {
    content: '';
    position: absolute;
    top: 0;
    right: -15px;
    bottom: -15px;
    left: -15px;
    border-radius: 4px;
    background-color: #fff; }
  .isActive .cardBottomPortion {
    opacity: 1;
    transform: scale(1); }
  .cardBottomPortion .forecastsWrapper {
    display: flex;
    flex-wrap: no-wrap;
    flex-direction: row;
    align-items: flex-end;
    height: 100%;
    padding-left: 0;
    margin-bottom: -22px; }
